import React, { ChangeEvent, useState } from 'react';
import './App.css';
import { getNumbers } from './utils';
import { Pagination } from './components/Pagination';

// eslint-disable-next-line @typescript-eslint/no-unused-vars
const items = getNumbers(1, 42)
  .map(n => n);

export const App: React.FC = () => {
  const [itemsPerPage, setItemsPerPage] = useState(5);
  const [currentPage, setCurrentPage] = useState(1);

  const setPerPage = (event: ChangeEvent<HTMLSelectElement>): void => {
    const newValue = +event.target.value;

    setItemsPerPage(newValue);
    setCurrentPage(1);
  };

  const startItem = (currentPage - 1) * itemsPerPage;
  const lastItem = (startItem + itemsPerPage) > items.length
    ? items.length : startItem + itemsPerPage;
  const visibleItems = [...items].slice(startItem, lastItem);

  const totalPages = Math.ceil(items.length / itemsPerPage);

  // const handlePrevPage = () => {
  //   if (currentPage > 1) {
  //     setCurrentPage(currentPage - 1);
  //   }
  // };

  // const handleNextPage = () => {
  //   if (currentPage < totalPages) {
  //     setCurrentPage(currentPage + 1);
  //   }
  // };

  const handlePageChange = (pageNumber: number) => {
    setCurrentPage(pageNumber);
  };

  return (
    <div className="container">
      <h1>Items with Pagination</h1>

      <p className="lead" data-cy="info">
        {`Page ${currentPage} (items ${startItem + 1} - ${lastItem} of ${items.length})`}
      </p>

      <div className="form-group row">
        <div className="col-3 col-sm-2 col-xl-1">
          <select
            data-cy="perPageSelector"
            id="perPageSelector"
            className="form-control"
            onChange={setPerPage}
            value={itemsPerPage}
          >
            <option value="3">3</option>
            <option value="5">5</option>
            <option value="10">10</option>
            <option value="20">20</option>
          </select>
        </div>

        <label htmlFor="perPageSelector" className="col-form-label col">
          items per page
        </label>
      </div>
      <Pagination
        items={visibleItems}
        currentPage={currentPage}
        // handlePrevPage={handlePrevPage}
        // handleNextPage={handleNextPage}
        handlePageChange={n => handlePageChange(n)}
        totalPages={totalPages}
      />
    </div>
  );
};

export default App;
